import jwtDecode from "jwt-decode";
import router from "@/router";
import { login, findById, tokenRegeneration, logout } from "@/api/member";

const memberStore = {
    namespaced: true,
    state: {
        isLogin: false,
        isLoginError: false,
        userInfo: null,
        isValidToken: false,
    },
    getters: {
        checkUserInfo: function (state) {
            return state.userInfo;
        },
        checkToken: function (state) {
            return state.isValidToken;
        },
    },
    mutations: {
        SET_IS_LOGIN: (state, isLogin) => {
            state.isLogin = isLogin;
        },
        SET_IS_LOGIN_ERROR: (state, isLoginError) => {
            state.isLoginError = isLoginError;
        },
        SET_IS_VALID_TOKEN: (state, isValidToken) => {
            state.isValidToken = isValidToken;
        },
        SET_USER_INFO: (state, userInfo) => {
            state.isLogin = true;
            state.userInfo = userInfo;
        },
    },
    actions: {
        async userConfirm({ commit }, user) {
            console.log(user)
            await login(
                user,
                ({ data }) => {
                    console.log(data.message)
                    if (data.message === "success") {
                        let accessToken = data["access-token"];
                        let refreshToken = data["refresh-token"];
                        console.log("login success token created!!!! >> ", accessToken, refreshToken);
                        commit("SET_IS_LOGIN", true);
                        commit("SET_IS_LOGIN_ERROR", false);
                        commit("SET_IS_VALID_TOKEN", true);
                        sessionStorage.setItem("access-token", accessToken);
                        sessionStorage.setItem("refresh-token", refreshToken);
                    } else {
                        commit("SET_IS_LOGIN", false);
                        commit("SET_IS_LOGIN_ERROR", true);
                        commit("SET_IS_VALID_TOKEN", false);
                    }
                },
                (error) => {
                    console.log(error);
                }
            );
        },
        async getUserInfo({ commit, dispatch }, token) {
            let decodeToken = jwtDecode(token);
            // console.log("2. getUserInfo() decodeToken :: ", decodeToken);
            await findById(
                decodeToken.userid,
                ({ data }) => {
                    if (data.message === "success") {
                        commit("SET_USER_INFO", data.userInfo);
                        // console.log("3. getUserInfo data >> ", data);
                    } else {
                        console.log("Ïú†Ï†Ä Ï†ïÎ≥¥ ÏóÜÏùå!!!!");
                    }
                },
                async (error) => {
                    console.log("getUserInfo() error code [ÌÜ†ÌÅ∞ ÎßåÎ£åÎêòÏñ¥ ÏÇ¨Ïö© Î∂àÍ∞ÄÎä•.] ::: ", error.response.status);
                    commit("SET_IS_VALID_TOKEN", false);
                    await dispatch("tokenRegeneration");
                }
            );
        },
        async tokenRegeneration({ commit, state }) {
            console.log("ÌÜ†ÌÅ∞ Ïû¨Î∞úÍ∏â >> Í∏∞Ï°¥ ÌÜ†ÌÅ∞ Ï†ïÎ≥¥ : {}", sessionStorage.getItem("access-token"));
            await tokenRegeneration(
                JSON.stringify(state.userInfo),
                ({ data }) => {
                    if (data.message === "success") {
                        let accessToken = data["access-token"];
                        console.log("Ïû¨Î∞úÍ∏â ÏôÑÎ£å >> ÏÉàÎ°úÏö¥ ÌÜ†ÌÅ∞ : {}", accessToken);
                        sessionStorage.setItem("access-token", accessToken);
                        commit("SET_IS_VALID_TOKEN", true);
                    }
                },
                async (error) => {
                    // HttpStatus.UNAUTHORIZE(401) : RefreshToken Í∏∞Í∞Ñ ÎßåÎ£å >> Îã§Ïãú Î°úÍ∑∏Ïù∏!!!!
                    if (error.response.status === 401) {
                        console.log("Í∞±Ïã† Ïã§Ìå®");
                        // Îã§Ïãú Î°úÍ∑∏Ïù∏ Ï†Ñ DBÏóê Ï†ÄÏû•Îêú RefreshToken Ï†úÍ±∞.
                        await logout(
                            state.userInfo.userid,
                            ({ data }) => {
                                if (data.message === "success") {
                                    console.log("Î¶¨ÌîÑÎ†àÏãú ÌÜ†ÌÅ∞ Ï†úÍ±∞ ÏÑ±Í≥µ");
                                } else {
                                    console.log("Î¶¨ÌîÑÎ†àÏãú ÌÜ†ÌÅ∞ Ï†úÍ±∞ Ïã§Ìå®");
                                }
                                alert("RefreshToken Í∏∞Í∞Ñ ÎßåÎ£å!!! Îã§Ïãú Î°úÍ∑∏Ïù∏Ìï¥ Ï£ºÏÑ∏Ïöî.");
                                commit("SET_IS_LOGIN", false);
                                commit("SET_USER_INFO", null);
                                commit("SET_IS_VALID_TOKEN", false);
                                router.push({ name: "login" });
                            },
                            (error) => {
                                console.log(error);
                                commit("SET_IS_LOGIN", false);
                                commit("SET_USER_INFO", null);
                            }
                        );
                    }
                }
            );
        },
        async userLogout({ commit }, userid) {
            await logout(
                userid,
                ({ data }) => {
                    if (data.message === "success") {
                        commit("SET_IS_LOGIN", false);
                        commit("SET_USER_INFO", null);
                        commit("SET_IS_VALID_TOKEN", false);
                    } else {
                        console.log("Ïú†Ï†Ä Ï†ïÎ≥¥ ÏóÜÏùå!!!!");
                    }
                },
                (error) => {
                    console.log(error);
                }
            );
        },
    },
};

export default memberStore;
